import {
     
    VIEW_FOOT, 
    VIEW_LOADING, ADD_ITEM, CLEAR_USER, UPDATE_DETAIL, UPDATE_USER, UPDATE_HOME,CHANGE_ITEM,REMOVE_ITEM,CLEAR_BUYCAR,
} from './types'

export default {

   
    [VIEW_FOOT]: (state, payload) => state.bFoot = payload,
    [VIEW_LOADING]: (state, payload) => state.bLoading = payload,
  
    [UPDATE_HOME]: (state, payload) => state.home = payload,
  
    // [UPDATE_FOLLOW]: (state, payload) => state.follow = payload,
  
    // [UPDATE_COLUMN]: (state, payload) => state.column = payload,
  
    // [UPDATE_LOGIN]: (state, payload) => state.login = payload,
  
    [UPDATE_DETAIL]: (state, payload) => state.detail = payload,
    [ADD_ITEM]: (state, payload) => {state.buycar = payload;
      
    },
    [CHANGE_ITEM]: (state, payload) => {state.buycar = payload;
      window.localStorage.setItem("news_goods",JSON.stringify(state.buycar))
    },

    [REMOVE_ITEM]:(state,payload)=>{state.buycar=payload;
      window.localStorage.setItem("news_goods",JSON.stringify(state.buycar))
      
    },
        
      
      [CLEAR_BUYCAR]:(state,payload)=>{
        state.buycar=payload
        window.localStorage.removeItem("news_goods")
      },
        
      

    [UPDATE_USER]: (state, payload) => state.user = payload,
    [CLEAR_USER]: (state, payload) => {
        window.localStorage.removeItem("news_app")
        window.localStorage.removeItem("news_goods")
        state.user = { err: 1 } 
    },
  }