import * as types from './types'

import axios from '../plugins/axios';


export default {
    [types.UPDATE_HOME]:({commit,state},payload)=>{
        axios({
            url:'/api/home',
            
        }).then(
            res=>commit(types.UPDATE_HOME,res.data.data)
        )
    },
    [types.UPDATE_DETAIL]: ({ commit, state }, payload) => {
        
        let { dataName, id } = payload;
        console.log(payload)
        axios({
          url: `/api/${dataName}/${id}`
        }).then(
          res => commit(types.UPDATE_DETAIL, res.data.data)
        )
      },
      // [types.UPDATE_LOGIN]: ({ commit, state }, payload) => {
        
      //   let {username,password} = payload
        
      //   axios({
      //     url: '/api/login',
      //     method:'post',
      //     data:{
      //       username:username,
      //       password:password
      //     }
      //   }).then(
      //    res=>
      //         commit(types.UPDATE_LOGIN, res.data)
      //   )
      // },
    [types.UPDATE_USER]: ({ commit, state }, payload) => {
        //actions->//抓取成功或者失败的数据，写入state, 同步localstorage->返回回执信息
        
        let {username,password,icon} = payload
        console.log(payload)
       return axios({
          url: '/api/login',
          method:'post',
          data:{
                  username,
                  password,
                  icon
                }
        }).then(
          res => {
            console.log(res.data)
            commit('UPDATE_USER', res.data);
            window.localStorage.setItem('news_app',JSON.stringify(res.data))
            
            return {err:res.data.err,msg:res.data.msg}
          }
        )
      },
      [types.ADD_ITEM]: ({ commit, state }, payload) => {
        let local = window.localStorage.getItem('news_app');
        if(local){
          let arr =[...state.buycar];
        let find = false;
        arr.forEach((item,index)=>{
          if(item._id==payload._id){
            item.cart_number++ //数量增加
            find=true;
            
          }
        });
        if(!find){
          payload.cart_number=1
          arr.push(payload)
        }
        commit('ADD_ITEM',arr)
        window.localStorage.setItem('news_goods',JSON.stringify(arr))
        return  {back:1}
        }
        
      },
      [types.CHANGE_ITEM]: ({ commit, state }, payload) => {
        
        state.buycar.forEach((item,index)=>{
          if(item._id==payload._id){
            item.cart_number+=payload.cart_number;
            if(item.cart_number<1){
              item.cart_number=1
            }
          }
        });
        commit(types.CHANGE_ITEM,[...state.buycar])
      },
      [types.REMOVE_ITEM]: ({ commit, state }, payload) => {
        state.buycar.forEach((item,index)=>{
          if(item._id==payload._id){
           state.buycar.splice(index,1)
          }
        });
        commit(types.REMOVE_ITEM,[...state.buycar])
        
      },
      [types.CLEAR_BUYCAR]: ({ commit, state }, payload) => {
       commit('CLEAR_BUYCAR',[])
      }
}