<template>
  <div id="app">
    <Loading v-if="$store.state.bLoading"></Loading>
    <transition :name="transitionName">
      <keep-alive>
        <router-view class="router-view"></router-view>
    </keep-alive>  
    
  </transition>
  <Footer v-if="bFoot"></Footer>
  </div>
</template>

<script>
import Footer from './components/Footer'
import Loading from './components/Loading'
import {mapState} from 'vuex'
export default {
  name: 'app',
  data(){
    return{
      transitionName:'slide-left'
    }
  },
  components: {
    Footer,Loading
  },
  watch:{
    $route:{
      // 监听路由的路径，可以通过不同的路径去选择不同的切换效果
      $route(to,from){
      if (to.meta.index > from.meta.index) {  //外---》内
                    this.transitionName = 'slide-left'
                } else if (to.meta.index < from.meta.index) {   //内---》外
                    this.transitionName = 'slide-right'
                } else {
                    this.transitionName = ''   //同级 无过渡效果
                }
    },
      handler(nextRoute,prevRoute){//属性检测,路由观测
        // console.log(nextRoute.path)
        let path = nextRoute.path;
        if(/home/.test(path)){
          
         this.$store.commit('VIEW_FOOT',true)
        }
        if(/user/.test(path)){
          
         this.$store.commit('VIEW_FOOT',true)
        }
        if(/login|reg|detail|car|order/.test(path)){
          
          this.$store.commit('VIEW_FOOT',false)
        }
      },
      immediate:true
    },
    
    
},
computed:mapState([
    'bFoot'
  ])
}
</script>

<style>
.router-view{
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        bottom: 0;
        margin: 0 auto;
        -webkit-overflow-scrolling: touch;
    }
.slide-right-enter-active,
    .slide-right-leave-active,
    .slide-left-enter-active,
    .slide-left-leave-active{
        height: 100%;
        will-change: transform;
        transition: all 500ms;
        position: absolute;
        backface-visibility: hidden;
    }
    .slide-right-enter{
        opacity: 0;
        transform: translate3d(-100%, 0, 0);
    }
    .slide-right-leave-active{
        opacity: 0;
        transform: translate3d(100%, 0, 0);
    }
    .slide-left-enter{
        opacity: 0;
        transform: translate3d(100%, 0, 0);
    }
    .slide-left-leave-active{
        opacity: 0;
        transform: translate3d(-100%, 0, 0);
    }
</style>
