import Vue from 'vue';
import VueRounter from 'vue-router'; // 引入
Vue.use(VueRounter) //  安装插件

//import 编译前 去加载的
import Home from '../pages/Home.vue'
let Car = () => import('@/pages/Car')
let Login = () => import('@/pages/Login')
let Reg = () => import('@/pages/Reg')
let User = () => import('@/pages/User')
let ErrorPage = () => import('@/pages/ErrorPage')
let Detail = () => import('@/pages/Detail')
let Logout = () => import('@/pages/Logout')
let Order = () => import('@/pages/Order')
let Class = () => import('@/pages/Class')
// import Car from '../pages/Car.vue'
// import Column from '@/pages/Column.vue' //别名 webpack自动配好的
// import Login from '../pages/Login.vue'
// import Reg from '../pages/Reg.vue'
// import User from '../pages/User'
// import ErrorPage from '../pages/ErrorPage'
// import Detail from '../pages/Detail'
// import Logout from '../pages/Logout'
// import Order from '../pages/Order'



// 配置路由  规则自上而下 排他性匹配
let routes=[
    {path:'/home',component:Home},
    // {path:'/Follow',component:Follow},
    {path:'/login',component:Login},
    {path:'/reg',component:Reg},
    {path:'/user',component:User},
    {path:'/logout',component:Logout},
    {path:'/detail/:id',component:Detail,props: (route)=>({id:route.params.id, dataName:route.query.dataName})},
    {path:'/car',component:Car},
    {path:'/Order',component:Order},
    {path:'/Class',component:Class},
    {path:'/',redirect:'/home'},
    {path:'*',component:ErrorPage},
    
]

export default new VueRounter({
    mode:'hash',
    routes:routes
})